import React, { Component } from 'react';
import {GridList, GridTile} from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import StarBorder from 'material-ui/svg-icons/toggle/star-border';

class GridListImage extends Component {
  render() {

    const styles = {
      root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
      },
      gridList: {
        width: 165,
        height: 'auto',
        overflowY: 'auto',
        margin: 5,
      },
    };



    return (
      <div style={styles.root}>
        <GridList
          cellHeight={'auto'}
          style={styles.gridList}
          cols={1}
          padding={10}
        >
          { this.props.tilesData.map((tile, index) => (
            <GridTile
              key={tile.id}
              title={tile.id}
              actionIcon={<IconButton><StarBorder color="white" /></IconButton>}
            >
            <a href='#' onClick={ this.props.onSelectChange } data-id={ index }>
              <img src={tile.url} alt='img' className="img--responsive" />
            </a>
            </GridTile>
          )) }
        </GridList>
      </div>
    );
  }
}

export default GridListImage;



