import React, { Component } from 'react';
import './Header.css';
import AppBar from 'material-ui/AppBar';

class Header extends Component {
  render(){
    return (
      <header className="header">
        <AppBar
          title="Beta OCR Cloud Engine"
          showMenuIconButton={false}
        />
      </header>
    );
  }
}

export default Header;



