import React, { Component } from 'react';
import TextField from 'material-ui/TextField';

class Footer extends Component {
    // onChange
  render(){
    return (
        <aside className="card form__wrapper">
            <form className="form__right" action="/" method="post">
                <TextField
                    hintText="Champ facture 1"
                    floatingLabelText="Facture 01"
                    fullWidth={true}
                />
                <TextField
                    hintText="Champ facture 2"
                    floatingLabelText="Facture 02"
                    fullWidth={true}
                />
                <TextField
                    hintText="Champ total"
                    floatingLabelText="Total"
                    fullWidth={true}
                />
            </form>
        </aside>
    );
  }
}

export default Footer;



