import React, { Component } from 'react';
import SideBar from './SideBar';
import EditForm from './EditForm';
import CanvasImage from './CanvasImage';

class Main extends Component {
    constructor() {
        super();
        this.tilesData=[];

        this.state = {
            index: 0,
            src: ''
        }

        // Bind event
        this.onSelectChange = this.onSelectChange.bind(this);
    }

    componentDidMount() {
        const resp = this.fetchData();
        
        resp.then((data) => {
            this.tilesData=data;
            const src = data[0].url;

            CanvasImage.init(src);
            this.setState({
                src
            });
        });

    }

    fetchData() {
        return fetch('http://testpf-njarasoa.rhcloud.com/get/dummy/data')  
            .then(res => res.text())
            .then(text => {
                const objRes = JSON.parse(text);
                const tab = objRes.map((curr, index, arr) => {
                    return {
                        url: curr.thumbnail.url,
                        id: index
                    };
                });
                return tab;
            });
    }

    onSelectChange(e) {
        e.preventDefault();
        const index = e.currentTarget.getAttribute('data-id');
        const src = e.currentTarget.firstElementChild.src;
        
        this.setState({
            index,
            src
        });
        CanvasImage.init(src);
    }
    
    render() {
        return (
            <section>
                <SideBar tilesData={ this.tilesData } onSelectChange={ this.onSelectChange }/>
                <main>
                    <div className="row">
                        <div className="large-8 columns">
                            <canvas id='canvas'></canvas>
                        </div>
                        <div className="large-4 columns">
                            <EditForm/>
                        </div>
                    </div>
                </main>
            </section>
        );
    }
}

export default Main;



